
.notif{
  margin-left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  position: absolute;
  z-index: 10;
}
.center{
  text-align: center;
}

































.consoleMes{
   text-align: left; 
   border:solid;
   border-width:1px;
   border-color:lightgray;
   border-radius:5px;
   padding-left: 10px;
   padding-top:20px;
   padding-bottom:20px;
   color:white;
   background-color:rgb(36, 29, 29);
   font: 0.8em 'Andale Mono', Consolas, 'Courier New';
   font-weight: bold;
   font-size: 1.1em;
   min-height: 100px;
   overflow-y: scroll;
}
#console-input{
    max-height: 300px;
}

.direction{
    border:solid;
    border-color:lightgray;
    border-width: 1px;
    border-radius: 50%;
}
.touched{
    background-color:lightblue;
}
.command{
    text-align: center;
}

.logo-vue{
    width: 30px;
    margin-left:15px;
}

.logo{
  margin-left:12px;
}
#logo-robot{
  width: 35px;
  margin-top:7px;
  margin-left:16px;
}
#logo-connection{
  width: 16px;
  margin-top:5px;
  margin-left:30px;
}
#logo-battery{
  width:50px;
  margin-left: 30px;
}
#led-connection{
  width:20px;
  height: 20px;
  padding-left:10px;
  margin-left:10px;
}

.led{
    border-radius: 50%;
    background-color: red;
    border:solid;
    border-width: 2px;
}
.light{
    background-color: rgb(0, 255, 0);
}

#fps{
   position: absolute;
   top:10px;
   left:20px;
   color:red;
}
#video{
   width: 100%;
   -webkit-user-select: none;
   -moz-user-select: none;
   -o-user-select: none;
   -ms-user-select: none;
       user-select: none;
}
#video:hover
 {
   cursor:crosshair;
}
.clikable:hover
 {
   cursor:pointer;
}
.position_place{
  border:solid;
  border-color: lightblue;
  padding : 10px;
  border-width: 1px;
}

.loading{
    padding-top: 10%;
    position:fixed;
    z-index: 10;
    width:100%;
    height: 100%;
    background-color: rgb(46, 37, 37);
    color: rgba(255, 255, 255, 0.788);
}   

.center{
    text-align: center;

    width: 100%;
}
